






import Vue from 'vue';
import UserIcon from './icons/UserIcon.vue';
const icons: { [name: string]: Vue.Component } = {
    userIcon: UserIcon,
};
interface Data {
  icon: Vue.Component;
}
export default Vue.extend({
  props: {
    name: { type: String, default: 'userIcon' },
    size: { type: Number, default: 24 },
  },
  data(): Data {
    return { icon: icons[this.name] };
  },
});
